.Cardlist{
    display: grid;
    grid-gap: 1em;
    grid-template-columns: 1fr 1fr 1fr;
}
@media(max-width:862px) {
    .Cardlist{
        grid-template-columns: 1fr 1fr;
    }
}@media(max-width:416px) {
    .Cardlist{
        grid-template-columns: 1fr;
    }
}
h1{
    text-align: center;
    grid-column: 1 / 4;
}